import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { BaseComponent } from 'app/base/base.component';
import { PermissionType } from 'core/constants';

@Component({
  selector: 'app-page-not-found',
  templateUrl: './page-not-found.component.html',
  styleUrls: ['./page-not-found.component.css']
})
export class PageForbiddenComponent extends BaseComponent implements OnInit {

  constructor(private router: Router) {super(); }


  ngOnInit() {
    this.loadAdminDataIfLoggenIn();
  }
  isRouteToPage(item){
    if(item.permissionType==PermissionType.NOACCESS){
      return false;
     }else{
      return true;
     } 
  }
routeToPage(item){
  debugger
   if(item.permissionType==PermissionType.NOACCESS){
    return false;
   }
  if(item.pageId==1){
    this.router.navigate(['/gym/profile']);
  }
  else if(item.pageId==2){
    this.router.navigate(['/gym/classes']);
  }
  else if(item.pageId==3){
    this.router.navigate(['/gym/schedule']);
  }
  else if(item.pageId==4){
    this.router.navigate(['/gym/payment']);
  }
  else if(item.pageId==5){
    this.router.navigate(['/gym/bookings']);
  }
  else if(item.pageId==6){
    this.router.navigate(['/gym/transaction']);
  }
  else if(item.pageId==7){
    this.router.navigate(['/gym/customers']);
  }
  else if(item.pageId==8){
    this.router.navigate(['/listPackage']);
  }
  else if(item.pageId==9){
    this.router.navigate(['/coupons']);
  }
  
  return true;
}
  
}



